@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  min-height: 100vh;
  background: no-repeat center / cover ;
  color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Fira Mono', monospace;
  position: relative;

}
body::before{
  content: "";
  display: block;
  background-color: rgba(0, 0, 0, .7);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
#root{
  padding: 2rem 1rem;
  width: 100%;
  max-width: 60rem;

}

#app {
  width: 100%;
  text-align: center;
  margin-bottom: 7rem;
}

.quote{
  font-size: 2rem;
  margin-bottom: 4rem;
}

.author{
  font-size: 1.5rem;
  border-radius: 10px;
}
.buttons-container{
  margin-top: 7rem;
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
}
button{
  outline: none;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  border: 2px solid rgb(6, 74, 175);
  background-color: rgb(6, 74, 175);
  padding: .5rem 1rem;
  

}

button:hover{
  background-color: transparent;
 
}


